import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { fetchUsers } from '../actions'




function UsersContainer ({ userData, fetchUsers }) {

  const [number, setNumber] = React.useState({
    value: 1,
  })
 
  useEffect(() => {
    
    fetchUsers();
   
  }, [fetchUsers])

  

  function KeyPress(e){
    if(e.keyCode === 13){
      setNumber({ value: e.target.value });
      console.log(number)
      }
  }

let id=number.value;

  return userData.loading ? (
 
<div id="typing-loader"></div>
  ) : userData.error ? (
    <h2>{userData.error}</h2>
  ) : (
    <div>
      
      <div>
  
      {userData && 
    userData.users &&
    userData.users.map(user =>  {if(user.id==id){return <div key={user.id}>
   
    <h2>Users List with ID: {user.id} </h2>{id} <center><p style={{fontSize:'3rem'}}>{user.name}</p></center>
     </div>} 
    else return null; 
    }
   
    )
    
    }
 
      </div>
      <div className="centralize-About">
        <label>Search:</label>
        <input className="inputUsers" type="text" defaultValue="1" onKeyDown={KeyPress}  ></input>
        <button>Search</button>
        </div>
    </div>
  )
        } 


    

const mapStateToProps = state => {
  return {
    userData: state.user
  }
}

const mapDispatchToProps = dispatch => {
  return {
    fetchUsers: () => dispatch(fetchUsers())
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UsersContainer)